.SearchBox {
    position: relative;
    top: -150px;
    margin: 200px auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 70px;
    height: 65px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-transition: .8s;
    transition: .8s;
}
.SearchBox:hover{
    width: 500px;
}
.SearchBox::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0px;
    width: 10px;
    height: 100%;
    background: -webkit-gradient(linear,left top, left bottom,from(#fff),color-stop(#fff),to(#e3e3e3));
    background: linear-gradient(#fff,#fff,#e3e3e3);
    z-index: 1;
    -webkit-filter: blur(1px);
            filter: blur(1px);
}
.SearchBox::after{
    content: '';
    position: absolute;
    top: 0;
    right: -1px;
    width: 10px;
    height: 100%;
    background: #9d9d9d;
    z-index: 1;
    -webkit-filter: blur(1px);
            filter: blur(1px);
}
.shadow {
    position: absolute;
    top: 0px;
    left: -53px;
    width: calc(100% + 50px);
    height: 300px;
    background: -webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,0.1)),
    color-stop(transparent),to(transparent));
    background: linear-gradient(180deg,rgba(0,0,0,0.1),
    transparent,transparent);
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: skew(45deg);
            transform: skew(45deg);
    pointer-events: none;
}
.shadow::before{
    content: '';
    position: absolute;
    left: 0%;
    width: 50px;
    height: 50px;
    background-color: #cfd1e1;
    z-index: 1;
}
.SearchBox input {
    display: block;
    /* display: none; */
    position: relative;
    /* text-align: center; */
    /* line-height: 30px; */
    width: 100%;
    height: 100%;
    border: none;
    padding: 10px 25px;
    outline: none;
    font-size: 1.3em;
    color: #555;
    /* 渐变色 */
    background: -webkit-gradient(linear, left top, left bottom, from(#dbdae1), to(#a3aaba));
    background: linear-gradient(#dbdae1, #a3aaba);
    /* 水平  垂直 模糊距离(虚实程度) 阴影尺寸 颜色 */
    -webkit-box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1),
      15px 15px 15px rgba(0, 0, 1402, 0.1),
      20px 20px 20px rgba(0, 0, 0, 0.1),
      30px 30px 15px rgba(0, 0, 0, 0.1),
      /* 可选。将外部阴影 (outset) 改为内部阴影。（内部阴影） */
      inset 1px 4px 2px#fff;
            box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1),
      15px 15px 15px rgba(0, 0, 1402, 0.1),
      20px 20px 20px rgba(0, 0, 0, 0.1),
      30px 30px 15px rgba(0, 0, 0, 0.1),
      /* 可选。将外部阴影 (outset) 改为内部阴影。（内部阴影） */
      inset 1px 4px 2px#fff;
}
